body, *
    margin: 0
    padding: 0
.app
    position: relative
    padding: 10px
    h1
        margin-top: 11px
        margin-left: 15px
    .add_task_btn
        position: absolute
        top: 20px
        right: 20px
        font-size: 24px
        padding: 5px 10px
        padding-bottom: 3px
        border: 1px solid black
        z-index: 3
        opacity: 1
        transition: 0.5s
        background: #f8f6f600
        border-radius: 10px
        display: flex
        align-items: center
        justify-content: center
        &.anim
            opacity: 0

            