.subtask_blok
    width: 100%
    position: absolute
    bottom: -1px
    transform: translateY(100%)
    left: -1px
    .show_subtask_btn
        background: black
        color: white
        border: none
        padding: 5px 10px
        font-size: 15px
    .subtask_list
        width: calc( 100% - 10px )
        border: 1px solid black
        border-top: none
        padding: 5px
        padding-top: 40px
        .add_subtask_btn
            background: white
            color: black
            font-size: 15px
            padding: 5px 10px
            border: 1px solid black
        .close
            position: absolute
            top: 0
            right: -2px
            border: none
            border-bottom: 1px solid black
            border-left: 1px solid black
            background: black
            color: white
            font-size: 18px
            padding: 3px 5px
            padding-bottom: 1px
        .subtask_form
            display: flex
            align-items: center
            justify-content: flex-start
            flex-direction: column
            position: relative
            padding-top: 15px
            width: max-content
            margin: 0 auto
            input
                padding: 5px
            button
                padding: 5px 10px
                &.close
                    padding: 3px 7px
                    padding-bottom: 1px
                    background: red
                    border: none
        .subtask
            position: relative
            width: calc( 90% - 45px )
            padding: 5px
            padding-right: 40px
            border: 1px solid black
            margin-bottom: 15px
            .title
                font-size: 18px
                font-weight: 500
                margin-bottom: 5px
            .text
                font-size: 14px
                font-weight: 400