.form
    top: 0
    left: 0
    position: absolute
    height: 100vh
    width: 100vw
    transform: translateY(-100%)
    opacity: 0.1
    background: white
    z-index: 2
    transition: 1s
    display: flex
    align-items: center
    justify-content: center
    flex-direction: column
    &.anim
        transform: translateY(0)
        opacity: 1
    input
        padding: 5px
    button
        padding: 5px 10px
    h2
        margin-bottom: 20px