.task_list
    display: flex
    align-items: flex-start
    justify-content: center
    flex-wrap: wrap
    .task
        width: 85%
        border: 1px solid black
        padding: 5px
        position: relative
        margin-top: 25px
        .delete
            position: absolute
            top: 0
            right: 0
            border: none
            border-bottom: 1px solid black
            border-left: 1px solid black
            padding: 3px 9px
            padding-bottom: 1px
            background: #e92d2d
            color: white
            font-size: 20px
            font-weight: 700
        .title
            font-size: 20px
            font-weight: 600
            margin-bottom: 5px
        .text
            font-size: 16px
            font-weight: 400